import { useEffect, useState } from 'react';
import { RechageApi } from 'utils/Api';
import { Table } from 'antd';
const { Column } = Table;

const Rechage = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    RechageApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' ').substring(0, 19),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' ').substring(0, 19),
        }))
      );
    });
  }, []);


  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="序号" dataIndex="id" />
      <Column title="会员手机" render={(_,record) => record.user.phone} />
      <Column title="充值金额" dataIndex="amount" />
      <Column title="实际付款" dataIndex="oriAmount" />
      <Column title="充值方式" render={(_,record) => record.payType.text} />
      <Column title="支付时间" render={(_, record) => record.createdAt === record.updatedAt ? '': record.updatedAt} />
      <Column title="支付说明" dataIndex="note" />
      <Column title="支付状态" render={(_, record) => record.status ? '已支付' : '未支付'} />
      <Column title="创建时间" dataIndex="createdAt" />
    </Table>
  );
};

export default Rechage;
